.modal_background {
	position: fixed;
	top: 0;
	left: 0;
	max-width: 100vw;
	max-height: 100vh;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background: rgba(0, 0, 0, 0.25);
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	pointer-events: all;
}

.modal_card {
	background: white;
	padding: 25px;
	border-radius: 5px;
	margin: 0 16px;
	/* pointer-events: ; */
}